.button {
  padding: 14px 28px 12px 28px;
  background: #2d0c2d;
  color: wheat;
  cursor: pointer;
  transition: .3s;
  position: relative;
  display: inline-block;
  transform: perspective(1px) translateZ(0); }
  .button:focus {
    transform: scale(0.95);
    transition: 0.2s; }
  .button:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    transform: scaleX(0);
    background: #1c081c;
    z-index: -1;
    transform-origin: 0 50%;
    transition: .3s; }
  .button:hover, .button:active {
    transition: .3s; }
    .button:hover:before, .button:active:before {
      transform: scaleX(1);
      transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); }

.button__secondary {
  padding: 14px 28px 12px 28px;
  color: #2d0c2d;
  box-sizing: border-box;
  box-shadow: 0px 0px 0px 1px #2d0c2d inset;
  position: relative;
  display: inline-block;
  transition: .3s; }
  .button__secondary:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: transparent;
    box-shadow: 0px 0px 0px 1px #1c081c inset;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition: .3s; }
  .button__secondary:hover, .button__secondary:active {
    transition: .3s; }
    .button__secondary:hover:before, .button__secondary:active:before {
      transform: scaleX(1);
      transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); }

.hover__center {
  position: relative; }
  .hover__center:after {
    position: absolute;
    height: 3px;
    width: 0;
    left: 50%;
    bottom: 10px;
    content: "";
    background-color: #960e29;
    transform: translateX(-50%); }
  .hover__center:hover:after {
    transition: 0.25s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    width: 20px;
    left: 50%;
    transform: translateX(-50%); }

